@import url(https://fonts.googleapis.com/css2?family=Exo+2:wght@100&display=swap);
* {
  box-sizing: border-box;
}

body {
  font-family: 'Trebuchet MS', 'Times New Roman', Times, sans-serif;
  margin: 0;
}
.app-wrapper{background-color:#000;background-image:url(/funbox/static/media/bg.da7154cc.png);background-repeat:no-repeat;background-position:center;background-size:cover}

.cards{display:flex;justify-content:center;flex-wrap:wrap}

.card-wrapper{width:320px;margin-left:20px;margin-right:20px;margin-top:40px}.card-wrapper .card{position:relative;cursor:pointer;max-height:480px;min-height:480px;color:#000000;-webkit-clip-path:polygon(100% 0%, 100% 100%, 0 100%, 0 8.5%, 12.5% 0);clip-path:polygon(100% 0%, 100% 100%, 0 100%, 0 8.5%, 12.5% 0);background-color:#f2f2f2;border:4px solid #1698d9;border-radius:10px;background-image:url(/funbox/static/media/cat.3ad87d7b.png);background-repeat:no-repeat;background-position:-24px 205px;padding:12px;transition:all 0.2s linear}.card-wrapper .card-header{font-size:16px;color:#666;margin-top:9px;margin-left:33px}.card-wrapper .card-body{margin-left:33px}.card-wrapper .card-body__title{font-size:48px;margin-top:17px;margin-bottom:0}.card-wrapper .card-body__with{font-size:24px}.card-wrapper .card-body__promo{margin-top:15px;color:#666;font-size:14px}.card-wrapper .card-weight{position:absolute;text-align:center;color:#ffffff;background-color:#1698d9;width:82px;height:82px;font-size:40px;right:12px;bottom:12px;border-radius:50%;padding-top:8px;transition:all 0.2s linear}.card-wrapper .card-weight__unit{font-size:15px}.card-wrapper .card::before{content:"";display:block;position:absolute;width:60px;height:4px;background-color:#1698d9;transform:rotate(-45deg);left:-10px;transition:all 0.2s linear}.card-wrapper .card:hover{border-color:#66c1ef}.card-wrapper .card:hover .card-weight,.card-wrapper .card:hover::before{background-color:#66c1ef}.card-wrapper .card-info{text-align:center;margin-top:15px}.card-wrapper .card-info__link{text-decoration:none;color:#1698d9;border-bottom:2px dashed #1698d9;transition:all 0.2s linear}.card-wrapper .card-info__link:hover{color:#66c1ef;border-color:#66c1ef}.card-wrapper .info-disabled{color:#9FA046}.card-wrapper .selected{border:4px solid #d91667}.card-wrapper .selected::before{content:"";display:block;position:absolute;width:60px;height:4px;background-color:#d91667;transform:rotate(-45deg);left:-10px;transition:all 0.2s linear}.card-wrapper .selected:hover{border-color:#ef669f}.card-wrapper .selected:hover .card-weight,.card-wrapper .selected:hover::before{background-color:#ef669f}.card-wrapper .selected .card-weight{background-color:#d91667}.card-wrapper .disabled{position:relative;border:4px solid #b3b3b3}.card-wrapper .disabled::before{content:"";display:block;position:absolute;width:60px;height:4px;background-color:#b3b3b3;transform:rotate(-45deg);left:-10px;transition:all 0.2s linear;z-index:10}.card-wrapper .disabled::after{content:"";cursor:default;display:block;position:absolute;border-radius:10px;top:0;bottom:0;left:0;right:0;background-color:rgba(255,255,255,0.6)}.card-wrapper .disabled .card-weight{background-color:#b3b3b3}.card-wrapper .disabled:hover{border:4px solid #b3b3b3}.card-wrapper .disabled:hover .card-weight,.card-wrapper .disabled:hover::before{background-color:#b3b3b3}

.app-container{position:relative;width:100%;min-height:100vh;display:flex;justify-content:center;align-items:center;flex-direction:column;color:white;background-color:rgba(0,0,0,0.8)}.app-title{display:block;width:100%;font-family:'Exo 2.0', sans-serif;font-weight:100;font-size:36px;text-align:center;margin:0}

